import { useEffect, useRef, useState } from "react";
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";

const RegisteredCentres = () => {
  const [centres, setCentres] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [selectedCentre, setSelectedCentre] = useState(null);
  const [showViewModal, setShowViewModal] = useState(null);

  const modalRef = useRef(null);
  const viewModalRef = useRef(null);

  useEffect(() => {
    const handleClickOutside = (event) => {
      if (
        modalRef.current &&
        !modalRef.current.contains(event.target) &&
        showModal
      ) {
        setShowModal(false);
      }
      if (
        viewModalRef.current &&
        !viewModalRef.current.contains(event.target) &&
        showViewModal
      ) {
        setShowViewModal(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);

    // ðŸ”‘ Scroll lock logic
    if (showViewModal) {
      document.body.style.overflow = "hidden"; // Disable background scroll
    } else {
      document.body.style.overflow = "auto"; // Enable background scroll
    }

    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
      document.body.style.overflow = "auto";
    };
  }, [showViewModal]);

  const fetchCentres = async () => {
    setLoading(true);
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/registeredCentres`
      );
      setCentres(res.data || []);
    } catch (err) {
      console.error("Error fetching centres:", err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchCentres();
  }, []);

 const handleDelete = async (id) => {
  const confirmDelete = window.confirm("Are you sure you want to delete this centre?");

  if (confirmDelete) {
    try {
      await axios.delete(`${process.env.REACT_APP_BACKEND_URL}/api/centres/${id}`);
      alert("Centre deleted successfully!");

      // Remove the deleted centre from the UI
      setCentres((prev) => prev.filter((c) => c._id !== id));
    } catch (err) {
      console.error(err);
      alert("Failed to delete the centre. Please try again.");
    }
  }
};


  //when clicking on reset it should call the email forgot password
  const handleForgotPassword = async (email) => {

    try {
      const response = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/centres/forgot-password`,
        { email } // pass clicked centre's email
      );

      if (response.status === 200) {
        toast.success(
          response?.data?.message || "Reset link sent successfully!"
        );
      }
    } catch (error) {
      console.error("Forgot password error:", error);
      toast.error(
        error.response?.data?.message || "Failed to send reset link."
      );
    }
  };


    const [currentPage, setCurrentPage] = useState(1);
  const centresPerPage = 10;

const indexOfLastCentre = currentPage * centresPerPage;
  const indexOfFirstCentre = indexOfLastCentre - centresPerPage;
  const currentCentres = centres.slice(
    indexOfFirstCentre,
    indexOfLastCentre
  );
  const totalCentresPages = Math.ceil(centres.length / centresPerPage);

    const handlePageChangeWithScroll = (page) => {
  handlePageChange(page); // your existing page change logic
  window.scrollTo({ top: 0, behavior: "smooth" }); // scroll to top smoothly
};

  const renderPagination = () => {
    const pages = [];
    const maxVisible = 3;
    let startPage = Math.max(1, currentPage - Math.floor(maxVisible / 2));
    let endPage = startPage + maxVisible - 1;

    if (endPage > totalCentresPages) {
      endPage = totalCentresPages;
      startPage = Math.max(1, endPage - maxVisible + 1);
    }
 pages.push(
    <li
      key="first"
      className={`page-item ${currentPage === 1 ? " disabled" : ""}`}
    >
      <button
        className="page-link"
        onClick={() => currentPage !== 1 && handlePageChangeWithScroll(1)}
       
      >
        First
      </button>
    </li>
  );

    // Prev Button
    pages.push(
      <li
        key="prev"
        className={`page-item ${currentPage === 1 ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => currentPage > 1 && handlePageChangeWithScroll(currentPage - 1)}
        >
          Prev
        </button>
      </li>
    );

    // Page Numbers
    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <li
          key={i}
          className={`page-item ${currentPage === i ? "active" : ""}`}
        >
          <button className="page-link" onClick={() => handlePageChangeWithScroll(i)}>
            {i}
          </button>
        </li>
      );
    }

    // Next Button
    pages.push(
      <li
        key="next"
        className={`page-item ${
          currentPage === totalCentresPages ? "disabled" : ""
        }`}
      >
        <button
          className="page-link"
          onClick={() =>
            currentPage < totalCentresPages && handlePageChangeWithScroll(currentPage + 1)
          }
        >
          Next
        </button>
      </li>
    );

      pages.push(
      <li
        key="last"
        className={`page-item ${currentPage ===totalCentresPages ? "disabled" : ""}`}
      >
        <button
          className="page-link"
          onClick={() => handlePageChangeWithScroll(totalCentresPages)}
        >
          Last
        </button>
      </li>
    );
    

    return pages;
  };

    const handlePageChange = (pageNumber) => setCurrentPage(pageNumber);



  return (
    <div className="dashboard-body__content">
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          <h4>Registered Centres</h4>
          {loading ? (
            <p>Loading centres...</p>
          ) : centres.length > 0 ? (
            <div className="table-responsive" style={{ marginTop: "-20px" }}>
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Centre Name</th>
                    <th>Centre Type</th>
                    <th>Email</th>
                    <th>Phone</th>
                    {/* <th>Address</th> */}
                    {/* <th>Documents</th> */}
                    <th className="text-center">Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {currentCentres.map((centre, i) => (
                    <tr key={centre._id}>
                      <td>{indexOfFirstCentre + i + 1}</td>
                      <td>{centre.centreName}</td>
                      <td>{centre.centreType}</td>
                      <td>{centre.email}</td>
                      <td>{centre.phoneNumber}</td>
                      {/* <td>{centre.address}</td> */}
                      {/* <td>
                        {centre.documents && centre.documents.length > 0 ? (
                          // eslint-disable-next-line jsx-a11y/anchor-is-valid
                          <a
                            href="#"
                            onClick={(e) => {
                              e.preventDefault();
                              setSelectedCentre(centre);
                              setShowModal(true);
                            }}
                          >
                            View Documents
                          </a>
                        ) : (
                          <span>No documents</span>
                        )}
                      </td> */}
                      <td className=" d-flex gap-2">
                        {/* View button - Always visible */}
                        <button
                          className="btn btn-sm btn-info"
                          onClick={() => {
                            setSelectedCentre(centre);
                            setShowViewModal("details");
                          }}
                          style={{ backgroundColor: "blue" }}
                        >
                          View
                        </button>

                        <button
                          className="btn btn-sm btn-success"
                          onClick={() => handleForgotPassword(centre.email)}
                        >
                          Reset
                        </button>

                        {/* Pending: Show Approve and Reject */}
                        {centre.status === "Pending" && (
                            <>
                              {/* <button
                                className="btn btn-sm btn-success"
                                onClick={() =>
                                  handleAction(centre._id, "Approved")
                                }
                                style={{ backgroundColor: "green" }}
                              >
                                Approve
                              </button> */}
                              <button
                                className="btn btn-sm btn-danger"
                                onClick={() =>
                                  handleDelete(centre._id)
                                }
                                style={{ backgroundColor: "red" }}
                              >
                                Delete
                              </button>
                            </>
                          )}

                        {/* Rejected: Show disabled Rejected button */}
                        {/* {centre.status === "Rejected" && (
                            <button
                              className="btn btn-sm btn-danger"
                              style={{ backgroundColor: "red" }}
                            >
                              Rejected
                            </button>
                          )} */}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p>No registered centres found.</p>
          )}
             {currentCentres.length > 0 && totalCentresPages>1 && (
                  <nav aria-label="Service pagination" className="mt-3">
                <ul className="pagination justify-content-center">
                  {renderPagination()}
                </ul>
              </nav>
                )}
        </div>

        {/* Modal for viewing documents */}
        {showModal && selectedCentre && (
          <div
            className="modal d-block"
            tabIndex="-1"
            role="dialog"
            style={{ backgroundColor: "rgba(0,0,0,0.5)" }}
          >
            <div className="modal-dialog modal-dialog-centered" role="document">
              <div
                className="modal-content"
                style={{ width: "550px" }}
                ref={modalRef}
              >
                <div className="modal-header">
                  <h5 className="modal-title">View Documents</h5>
                  <button
                    type="button"
                    className="btn-close"
                    onClick={() => setShowModal(false)}
                  ></button>
                </div>
                <div className="modal-body">
                  {selectedCentre.documents?.length > 0 ? (
                    <table className="table">
                      <thead>
                        <tr>
                          <th>SI.No</th>
                          <th>Doc Type</th>
                          <th>Action</th>
                        </tr>
                      </thead>
                      <tbody style={{ color: "black" }}>
                        {selectedCentre.documents.map((doc, index) => (
                          <tr key={index}>
                            <td>{index + 1}</td>
                            <td>{doc.docType}</td>
                            <td>
                              <a
                                className="btn btn-sm btn-primary"
                                href={doc.docFile}
                                target="_blank"
                                rel="noopener noreferrer"
                              >
                                View
                              </a>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  ) : (
                    <p>No documents found.</p>
                  )}
                </div>
              </div>
            </div>
          </div>
        )}

        {showViewModal && selectedCentre && (
          <div
            style={{
              position: "fixed",
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: "rgba(0,0,0,0.5)",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              zIndex: 1000,
            }}
          >
            <div
              ref={viewModalRef}
              style={{
                backgroundColor: "#fff",
                borderRadius: "10px",
                maxWidth: "600px",
                width: "90%",
                maxHeight: "95vh", // modal still limited to viewport height

                boxShadow: "0 0 10px rgba(0,0,0,0.3)",
                position: "relative",
                overflowY: "scroll",
              }}
              className="no-scrollbar"
            >
              <div
                style={{
                  backgroundColor: "#3a3a3a",
                  color: "#fff",
                  padding: "15px 20px",
                  fontSize: "18px",
                  fontWeight: "bold",
                   position:"sticky",
                   top:"0",
                   zIndex: 10,
                }}
              >
                Centre Details
                <button
                  onClick={() => setShowViewModal(false)}
                  style={{
                    position: "absolute",
                    top: "10px",
                    right: "10px",
                    background: "transparent",
                    border: "none",
                    fontSize: "22px",
                    color: "#fff",
                    cursor: "pointer",
                   
                   
                  }}
                >
                  &times;
                </button>
              </div>

              <div
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  padding: "20px",
                  gap: "20px",
                  color: "#333",
                }}
              >
                {/* Left Column */}
                <div style={{ flex: 1, minWidth: "250px" }}>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Centre Name</strong>
                    <p>{selectedCentre.centreName}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Centre Type</strong>
                    <p>{selectedCentre.centreType}</p>
                  </div>
                 
              
                </div>

                {/* Right Column */}
                <div style={{ flex: 1, minWidth: "250px" }}>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Email</strong>
                    <p>{selectedCentre.email}</p>
                  </div>
                  <div style={{ marginBottom: "15px" }}>
                    <strong>Phone</strong>
                    <p>{selectedCentre.phoneNumber}</p>
                  </div>
 </div>
              </div>
            </div>
          </div>
        )}
      </div>
      <ToastContainer position="top-right" autoClose={3000} />
    </div>
  );
};

export default RegisteredCentres;
